import stats from 'stats-js';
import browser from 'browser-detect';
//test fps
import $ from 'jquery';
require('bootstrap');
import niceScroll from 'jquery.nicescroll';
import slick from 'slick-carousel';
import ScrollMagic from 'scrollmagic';
import {
  TweenLite
} from 'gsap';

import News from './pages/News';
import Production from './pages/Production';
import IndexContent from './pages/IndexContent';
import Cooperation from './pages/Cooperation';

let pageName;
let hash;
let host;


export default class App {
  constructor() {

  }
  init() {

    this.browser = browser();
    // this.initStats();
    this.page = "";
    this.host = "http://ec2-54-238-37-155.ap-northeast-1.compute.amazonaws.com/";
    host = this.host;
    this.initPages();
    this.initNav();
    this.addListeners();
    this.animate();
    this.initSlider();
    this.initContact();

  }
  initContact(){
    // $('.playBtn').click(function(){
    //
    //   // $('#audio1').play();
    //
    // })

    $('input[type=checkbox]').change(function(){
      $('audio')[0].pause();
      if($(this).prop('checked') == true){

        switch ($(this).data('song')){
          case 1:
            $('#audio1')[0].play();
          break;
          case 2:
          $('#audio2')[0].play();

          break;
          case 3:
          $('#audio3')[0].play();
          break;
        }
      }else{
        switch ($(this).data('song')){
          case 1:
            $('#audio1')[0].pause();
          break;
          case 2:
          $('#audio2')[0].pause();

          break;
          case 3:
          $('#audio3')[0].pause();
          break;
        }

      }

    })
    $('.mp3__lyrics').niceScroll();
    $('.contact .button').click(function(){

              var sName = $('.contact__name').val();
              var sTel = $('.contact__tel').val();
              var sEmail = $('.contact__email').val();
              var sType = $('.contact__select').val();
              var sContent = $('.contact__question').val();
              var sJson = {
                name: sName,
                phone: sTel,
                email: sEmail,
                subject: sType,
                content: sContent,
              };
              console.log(sJson);
              $.ajax({
                type: "post",
                async: false,
                dataType: "json",
                url: host+"api/contactus/new",
                contentType: 'application/x-www-form-urlencoded; charset=UTF-8',
                data: sJson,
                success: function(msg) {
                  // console.log('gj', msg)

                  //do something
                },
                statusCode: {
                  200: function(su) {
                    // console.log(su)
                    alert('已完成送出');

                  },
                  400: function(response) {
                    // console.log(response);
                    var errorMsg = JSON.parse(response.responseText);
                    console.log(errorMsg.msg);
                    var msg="";
                    var obj =errorMsg.msg;
                    for(var x in obj){
                      msg += obj[x];
                    }
                    console.log(msg);
                    alert('error:'+msg);
                  }
                }
              });
    })
  }
  initPages() {
    var path = window.location.pathname;
    pageName = path.split("/").pop();
    // console.log(pageName);
    switch(pageName){
      case '':
        this.indexContent = new IndexContent(this);
      break;
      case 'index.html':
        this.indexContent = new IndexContent(this);
      break;
      case 'news.html':
        this.news = new News(this);
      break;
      case 'production.html':
        this.production = new Production(this);
      break;
      case 'cooperation.html':
        this.cooperation = new Cooperation(this);
      break;
    }

    hash = window.location.hash.substr(1);
    this.showPage();
    // if(hash!='') {
    //     // console.log(hash);
    // }


    var val = window.location.href;
    if(val.indexOf('?')!=-1)
    {
      var ary1 = val.split('?');
      var ary2 = ary1[1].split('&');
      var ary3 = ary2[0].split('=');
      var id = ary3[1];
      // console.log(id)
    }
  }
  initNav() {
      $('.nav__submenu').removeClass('is-active');
      $('.nav__item').hover(
        function(){
        //hover function
          if($('.nav__item').hasClass('is-active')){
            $('.nav__item').removeClass('is-active');
            $('.nav__submenu').removeClass('is-active');
          }
          $(this).addClass('is-active');
          // console.log();
          if($(this).children('ul.sub__nav__content').length!==0){
            $('.nav__submenu').addClass('is-active');
          }
        },
        function(){
        //out function
        $('.nav__item').removeClass('is-active');
        $('.nav__submenu').removeClass('is-active');
        //
        $('.nav__item').each(function(){
          var tempStr = $(this).children("a").attr('href');
          if(tempStr == pageName){
            $(this).addClass('is-active');
            if($(this).children('ul.sub__nav__content').length!==0){
              $('.nav__submenu').addClass('is-active');
            }
          }
        })
      })
      $('.nav__item').each(function(){

        var tempStr = $(this).children("a").attr('href');
        if(tempStr == pageName){
          $(this).addClass('is-active');
          if($(this).children('ul.sub__nav__content').length!==0){
            $('.nav__submenu').addClass('is-active');
          }
        }
      })
    $('.hamburger').click(function() {
      if ($('.hamburger').hasClass('is-active')) {
        $('.hamburger').removeClass('is-active');
        $('.nav__content').removeClass('is-active');
        $('.nav__shop').removeClass('is-active');
      } else {
        $('.hamburger').addClass('is-active')
        $('.nav__content').addClass('is-active');
        $('.nav__shop').addClass('is-active');
      }
    })

  }
  initSlider() {

  }
  //init Stats
  initStats() {
    this.stats = new stats();
    this.stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
    document.body.appendChild(this.stats.dom);
  }

  addListeners() {
    this.handlerAnimate = this.animate.bind(this);
    window.addEventListener('resize', this.resize.bind(this));
    window.addEventListener('keyup', this.keyup.bind(this));
    window.addEventListener('mousemove', this.mousemove.bind(this));
    window.addEventListener('click', this.click.bind(this));
    window.addEventListener('mouseup', this.mouseup.bind(this));
    window.addEventListener('touchend', this.mouseup.bind(this));
    window.addEventListener("hashchange", this.funcRef.bind(this), false);
  }


  // ---------------------------------------------------------------------------------------------
  // PUBLIC
  // ---------------------------------------------------------------------------------------------
  animate() {
    if (this.stats) this.stats.begin();
    this.update();
    if (this.stats) this.stats.end();
    this.raf = requestAnimationFrame(this.handlerAnimate);
  }
  update() {

  }

  // ---------------------------------------------------------------------------------------------
  // EVENT HANDLERS
  // ---------------------------------------------------------------------------------------------
  funcRef(){
    hash = window.location.hash.substr(1);
    this.showPage();
    if(this.news) this.news.hashchange(hash);
    if(this.production) this.production.hashchange(hash);
    if(this.cooperation) this.cooperation.hashchange(hash);
  }
  showPage(){
    if(hash!='') {
      // console.log(hash);
      $('.page-content').hide();
      $('.page-content.'+hash).show();
      $('.sub__nav__item a').each(function(){
        var nowHref = $(this).attr('href');
        var url = window.location.href;
        if(url.indexOf(nowHref)!== -1){
          $(this).css('color',"#888888");
        }else{
          $(this).css('color',"#FFFFFF");
        }
      })

    }else{
      // console.log('index');
      $('.page-content').hide();
      $('.page-content.index').show();
    }
    var $body = (window.opera) ? (document.compatMode == "CSS1Compat" ? $('html') : $('body')) : $('html,body');
		$body.animate({
			scrollTop: 0
		}, 600);
  }
  resize() {}

  keyup(e) {

  }
  mouseup(e) {

  }
  mousemove(e) {

  }

  click(e) {

  }

}
