  init() {

    $('.year-btn').click(function() {
      var year = $(this).data("year");
      console.log($(this).data("year"))
      $('.news-page__item, .albums-page__item').hide();
      $('.news-page__item, .albums-page__item[data-year=' + year + ']').show();
      $('.img__slider').slick('setPosition');
    })
    this.setSlider();

    $('.img__slider__img').click(function(){
      console.log($(this).attr('src'));
      $('.modal__img').attr('src',$(this).attr('src'));
      $('#newsModal').modal('show');
    })
  }
  setSlider() {
    $('.img__slider').slick({
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      prevArrow: false,
      nextArrow: false,
      dots: true
    })
    $('.cal-slider').slick({
      infinite: true,
      slidesToShow: 5,
      slidesToScroll: 1,
      responsive: [{
          breakpoint: 992,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1
          }
        },
        {
          breakpoint: 567,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        },
      ]
    })
  }
  refreshSlder() {
    $('.img__slider').slick('setPosition');
    $('.cal-slider').slick('setPosition');
  }
  hashchange(target) {
    this.refreshSlder();
  }
